import { Component, ViewEncapsulation } from '@angular/core';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./manager.component.html",
	styleUrls: ["./manager.component.css"], 
    encapsulation: ViewEncapsulation.None,
	animations: [ FLY_IN_OUT_ANIMATION ]
})

export class MangerComponent { 
	expanded:boolean = true;

	compModulePortal = sessionStorage.getItem('compModulePortal');
	compModuleAsset = sessionStorage.getItem('compModuleAsset');
	compModuleInventory = sessionStorage.getItem('compModuleInventory');
	compModuleProductcatalogue = sessionStorage.getItem('compModuleProductcatalogue');
	compModuleCustomer = sessionStorage.getItem('compModuleCustomer');
	compModuleInvoice = sessionStorage.getItem('compModuleInvoice');
	compModulePayroll = sessionStorage.getItem('compModulePayroll');
	compModulePm = sessionStorage.getItem('compModulePm');
	compModuleProduction = sessionStorage.getItem('compModuleProduction');
	compModuleHcm = sessionStorage.getItem('compModuleHcm');
	compModuleSales = sessionStorage.getItem('compModuleSales');
	compModuleService = sessionStorage.getItem('compModuleService');
	compModuleSocial = sessionStorage.getItem('compModuleSocial');
	compModuleActivity = sessionStorage.getItem('compModuleActivity');
	compModuleAccounting = sessionStorage.getItem('compModuleAccounting');
	compModuleMarketing = sessionStorage.getItem('compModuleMarketing');
	compModulePos = sessionStorage.getItem('compModulePos');
	empMisAccess = sessionStorage.getItem('empMisAccess');
}


