import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { LayoutModule } from '../../../layouts/layout.module';
import { DefaultComponent } from '../../default.component';
import { MangerComponent } from './manager.component';
import {  MatCardModule, MatExpansionModule } from '@angular/material';

const routes: Routes = [
    {
        "path": "",
        "component": DefaultComponent,
        "children": [
            {
                "path": "",
                "component": MangerComponent
            }
        ]
    }
];

@NgModule({
    imports: [
        CommonModule, RouterModule.forChild(routes), LayoutModule, MatCardModule, MatExpansionModule 
//       MatInputModule, MatNativeDateModule, MatSelectModule, MatSidenavModule, 
//		MatTableModule, MatSortModule, MatPaginatorModule,MatDatepickerModule, MatCheckboxModule, MatRadioModule,
    ],
	exports: [ RouterModule ],
	declarations: [ MangerComponent ]
})

export class ManagerModule {}


